<template>
    <el-container id="notebook-list" class="main-page-container">
        <el-header class="header">
            <el-button type="primary" class="button" @click="_createNoteBook"><i class="el-icon-plus"></i> </el-button>
        </el-header>
        <el-main class="main">
            <p class="list-title">({{total}})</p>
            <div class="list">
                <el-table :data="tableData" height="500" border style="width: 100%" row-class-name="table-row">
                    <el-table-column prop="title" label="">
                        <template slot-scope="scope">
                            <router-link class="table-link" :to="{ name: 'note', params: {notebook_id: scope.row.id} }">
                                <span>
                                    <i class="fa fa-book"></i>
                                    {{scope.row.title}}
                                </span>
                            </router-link>
                        </template>
                    </el-table-column>
                    <el-table-column prop="create_date_name" label="" width="150"></el-table-column>
                    <el-table-column label="" width="160">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="_editNoteBookTitle(scope.row)"></el-button>
                            <el-button size="mini" type="danger" @click="_deleteNoteBook(scope.row.id)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </el-main>
        <!--  -->
    </el-container>
</template>

<script>
import User from '@/servers/user'
import { mapState, mapActions, mapGetters } from 'vuex'

export default {
    data() {
        return {
            centerDialogVisible: false
        }
    },
    computed: {
        total () {
            return this.$store.getters.noteBookList.length
        },
        ...mapGetters({tableData: 'noteBookList'})
    },
    created() {
        this.checkLogin().then(res => {
            this.$store.dispatch('getNoteBookList')
        })
        .catch(res => {
            this.$message({
                message: '',
                type: 'error'
            })
            this.$router.push({path: '/login'})
        })
    },
    methods: {
        ...mapActions([
            'getNoteBookList',
            'createNoteBook',
            'updateNoteBook',
            'deleteNoteBook',
            'checkLogin'
        ]),
        // 
        _createNoteBook() {
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputPattern: /^.{1,30}$/,
                inputErrorMessage: '30'
            }).then(({ value }) => {
                this.createNoteBook({title: value})
                // this.$store.dispatch('getNoteBookList')
            }).catch(() => {     
            })
        },
        // 
        _editNoteBookTitle(row) {
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputValue: row.title,
                inputPattern: /^.{1,30}$/,
                inputErrorMessage: '30'
            }).then(({ value }) => {
                this.updateNoteBook({id: row.id, title: value})
                this.$store.dispatch('getNoteBookList')
            }).catch(() => {     
            })
        },
        // 
        _deleteNoteBook(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.deleteNoteBook({id: id})
                this.$store.dispatch('getNoteBookList')
            }).catch(() => {     
            })
        }
    }
}
</script>

<style lang="scss">
@import "../../assets/style/common";
#notebook-list {
    .header {
        .button {
            height: 40px;
        }
    }
    .main {
        .list-title {
            max-width: 1000px;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 12px;
            font-weight: bold;
        }
    }
    .list {
        max-width: 1000px;
        margin-left: auto;
        margin-right: auto;
        .table-link {
            width: 100%;
            display: inline-block;
            color: #909399;
            .fa {
                color: #909399;
            }
        }
        .table-row:hover {
            .table-link {
                color: #409EFF;
                .fa {
                    color: #409EFF;
                }
            }
        }
    }
}
</style>


